import { Inject, Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient, HttpParams } from '@angular/common/http';
import { ActivatedRouteSnapshot } from '@angular/router';
import { IResponseResult } from '../../../interfaces/iresponse-result';
import { APP_CONFIG, IAppConfig } from '../../../configs/app.config';
import { ISearchResult } from '../../../interfaces/isearch.result';
import { Video } from './video.model';

@Injectable()
export class VideoService {
    url = 'video/';

    constructor( @Inject(APP_CONFIG) public appConfig: IAppConfig,
        private http: HttpClient) {
        this.url = `${appConfig.WEBSITE_API_URL}${this.url}`;
    }

    resolve(route: ActivatedRouteSnapshot, state: Object) {
        const queryParams = route.queryParams;
        const keyword = queryParams.keyword;
        const isActive = queryParams.isActive;
        const page = queryParams.page;
        const pageSize = queryParams.pageSize;
        return this.search(keyword, isActive, page, pageSize);
    }

    insert(video: Video): Observable<IResponseResult> {
        return this.http.post(`${this.url}insert`, video) as Observable<IResponseResult>;
    }

    update(video: Video): Observable<IResponseResult> {
        return this.http.post(`${this.url}update`, video) as Observable<IResponseResult>;
    }

    delete(id: number): Observable<IResponseResult> {
        return this.http.delete(`${this.url}delete/${id}`) as Observable<IResponseResult>;
    }

    search(keyword: string, isActive?: boolean, page: number = 1, pageSize: number = 20): Observable<ISearchResult<Video>> {
        return this.http.get(`${this.url}search`, {
            params: new HttpParams()
                .set('keyword', keyword ? keyword : '')
                .set('isActive', isActive != null && isActive !== undefined ? isActive.toString() : '')
                .set('page', page ? page.toString() : '1')
                .set('pageSize', pageSize ? pageSize.toString() : this.appConfig.PAGE_SIZE.toString())
        }) as Observable<ISearchResult<Video>>;
    }
}
