import { Component, EventEmitter, OnInit, Output, ViewChild } from '@angular/core';
import { BaseFormComponent } from '../../../../base-form.component';
import { FormBuilder, Validators } from '@angular/forms';
import { UtilService } from '../../../../shareds/services/util.service';
import { NhModalComponent } from '../../../../shareds/components/nh-modal/nh-modal.component';
import { IResponseResult } from '../../../../interfaces/iresponse-result';
import { ToastrService } from 'ngx-toastr';
import { Video } from '../video.model';
import { VideoService } from '../video.service';
import { finalize } from 'rxjs/operators';
import { AppService } from '../../../../shareds/services/app.service';

@Component({
    selector: 'app-video-form',
    templateUrl: './video-form.component.html'
})

export class VideoFormComponent extends BaseFormComponent implements OnInit {
    @ViewChild('videoFormModal') videoFormModal: NhModalComponent;
    @Output() onSaveSuccess = new EventEmitter();
    video = new Video();
    videoTypes = [{id: 0, name: 'Youtube'}, {id: 1, name: 'Vimeo'}, {id: 2, name: 'Upload lên server'}];

    constructor(private fb: FormBuilder,
                private toastr: ToastrService,
                private utilService: UtilService,
                private appService: AppService,
                private videoService: VideoService) {
        super();
    }

    ngOnInit() {
        this.buildForm();
    }

    onFormModalShown() {
        this.utilService.focusElement('url');
        this.utilService.focusElement('videoId');
    }

    onFormModalHidden() {
        if (this.isModified) {
            this.onSaveSuccess.emit();
        }
    }

    add() {
        this.isUpdate = false;
        this.videoFormModal.show();
    }

    edit(video: Video) {
        this.isUpdate = true;
        this.video = video;
        this.model.patchValue(video);
        this.videoFormModal.show();
    }

    save() {
        const isValid = this.utilService.onValueChanged(this.model, this.formErrors, this.validationMessages, true);
        if (isValid) {
            this.video = this.model.value;
            this.isSaving = true;
            if (this.isUpdate) {
                this.videoService.update(this.video)
                    .pipe(finalize(() => this.isSaving = false))
                    .subscribe((result: IResponseResult) => {
                        this.toastr.success(result.message);
                        this.isModified = true;
                        this.model.reset(new Video());
                        this.videoFormModal.dismiss();
                    });
            } else {
                this.videoService.insert(this.video)
                    .pipe(finalize(() => this.isSaving = false))
                    .subscribe((result: IResponseResult) => {
                        this.toastr.success(result.message);
                        this.isModified = true;
                        this.model.reset(new Video());
                    });
            }
        }
    }

    private buildForm() {
        this.formErrors = this.utilService.renderFormError(['url', 'title', 'description', 'thumbnail', 'type']);
        this.validationMessages = {
            'url': {
                'required': 'Vui lòng nhập đường dẫn video',
                'maxLength': 'Đường dẫn video không được phép vượt quá 500 ký tự'
            },
            'title': {
                'required': 'Vui lòng nhập tiêu đề video',
                'maxLength': 'Tiêu đề video không được phép vượt quá 256 ký tự.'
            },
            'description': {
                'maxLength': 'Mô tả video không được phép vượt quá 500 ký tự.'
            },
            'thumbnail': {
                'maxLength': 'Thumbnail không được phép vượt quá 500 ký tự.'
            },
            'type': {
                'required': 'Vui lòng chọn loại video.'
            }
        };

        this.model = this.fb.group({
            'id': [this.video.id],
            'videoId': [this.video.videoId],
            'url': [this.video.url, [
                Validators.required,
                Validators.maxLength(256)
            ]],
            'title': [this.video.title, [
                Validators.required,
                Validators.maxLength(256)
            ]],
            'description': [this.video.description, [
                Validators.maxLength(500)
            ]],
            'thumbnail': [this.video.thumbnail, [
                Validators.maxLength(500)
            ]],
            'isActive': [this.video.isActive],
            'order': [this.video.order],
            'type': [this.video.type, [
                Validators.required
            ]],
        });
        this.model.valueChanges.subscribe(() => this.utilService.onValueChanged(this.model, this.formErrors, this.validationMessages));
    }
}
