import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

@Component({
    selector: 'nh-step',
    templateUrl: './nh-step.component.html'
})
export class NhStepComponent implements OnInit {
    @Input() step: number;
    @Input() title: string;
    @Input() description: string;
    @Input() isValid = true;
    @Input() isLoading = false;
    @Input() icon = '';
    @Input() backLabel = 'Quay lại';
    @Input() nextLabel = 'Tiếp theo';
    @Input() finishLabel = 'Hoàn thành';
    @Output() onNextClick = new EventEmitter();
    @Output() onBackClick = new EventEmitter();
    @Output() onFinishClick = new EventEmitter();
    isShow = false;
    isFinish = false;
    isLast = false;

    constructor() {
    }

    ngOnInit() {
    }

    next() {
        this.onNextClick.emit(this.step);
    }

    back() {
        this.onBackClick.emit(this.step);
    }

    finish() {
        this.onFinishClick.emit(this.step);
    }
}
