export class Video {
    id: number;
    videoId: string;
    title: string;
    url: string;
    description: string;
    thumbnail: string;
    isActive: boolean;
    order: number;
    type: number;

    constructor(id?: number, videoId?: string, title?: string, url?: string, description?: string, thumbnail?: string, isActive?: boolean, order?: number, type?: number) {
        this.id = id;
        this.videoId = videoId;
        this.title = title;
        this.url = url;
        this.description = description;
        this.thumbnail = thumbnail;
        this.isActive = isActive ? isActive : false;
        this.order = order;
        this.type = type ? type : 0;
    }
}
