import { Component, Inject, OnInit, ViewChild } from '@angular/core';
import { BaseListComponent } from '../../../base-list.component';
import { Video } from './video.model';
import { ActivatedRoute } from '@angular/router';
import { VideoService } from './video.service';
import { SpinnerService } from '../../../core/spinner/spinner.service';
import { IPageId, PAGE_ID } from '../../../configs/page-id.config';
import { AppService } from '../../../shareds/services/app.service';
import { ToastrService } from 'ngx-toastr';
import { IResponseResult } from '../../../interfaces/iresponse-result';
import { ISearchResult } from '../../../interfaces/isearch.result';
import * as _ from 'lodash';
import { VideoFormComponent } from './video-form/video-form.component';
import { map, finalize } from 'rxjs/operators';

@Component({
    selector: 'app-video',
    templateUrl: './video.component.html'
})

export class VideoComponent extends BaseListComponent<Video> implements OnInit {
    @ViewChild(VideoFormComponent) videoFormComponent: VideoFormComponent;
    isActive: boolean;

    constructor(@Inject(PAGE_ID) public pageId: IPageId,
                private route: ActivatedRoute,
                private toastr: ToastrService,
                private appService: AppService,
                private spinnerService: SpinnerService,
                private videoService: VideoService) {
        super();
    }

    ngOnInit() {
        this.appService.setupPage(this.pageId.WEBSITE, this.pageId.VIDEO, 'Quản lý Video', 'Danh sách video');
        this.listItems$ = this.route.data.pipe(map((result: { data: ISearchResult<Video> }) => {
            const data = result.data;
            this.totalRows = data.totalRows;
            return data.items;
        }));
    }

    search(currentPage: number) {
        this.currentPage = currentPage;
        this.isSearching = true;
        this.listItems$ = this.videoService.search(this.keyword, this.isActive, this.currentPage, this.pageSize)
            .pipe(finalize(() => this.isSearching = false),
                map((result: ISearchResult<Video>) => {
                    return _.map(result.items, (video: Video) => {
                        this.totalRows = result.totalRows;
                        return new Video(video.id, video.videoId, video.title, video.url, video.description,
                            video.thumbnail, video.isActive, video.order, video.type);
                    });
                }));
    }

    delete(id: number) {
        this.spinnerService.show('Đang xóa video. Vui lòng đợi...');
        this.videoService.delete(id)
            .subscribe((result: IResponseResult) => {
                this.toastr.success(result.message);
                this.search(this.currentPage);
            });
    }
}
